<template>
  <div class="about">
    <h1>{{ id ? "编辑" : "新建" }}视频</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="所属分类">
        <el-select v-model="model.categories" multiple>
          <el-option
            v-for="item in categories"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="视频标题">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="播放次数">
        <el-input v-model="model.times"></el-input>
      </el-form-item>
      <el-form-item label="图片">
        <el-upload
          class="avatar-uploader"
          :action="$http.defaults.baseURL + '/upload'"
          :show-file-list="false"
          :on-success="(res) => $set(this.model, 'img', res.url)"
        >
          <img v-if="model.img" :src="model.img" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {},
  },
  data() {
    return {
      model: { },
      categories: [],
    };
  },
  methods: {
    async save() {
      let res;
      if (this.id) {
        // 修改
        res = await this.$http.put(`rest/video/${this.id}`, this.model);
      } else {
        // 新增item
        res = await this.$http.post("rest/video", this.model);
      }
      console.log(res);
      this.$router.push("/video/list");
      this.$message({
        type: "success",
        message: "保存成功",
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/video/${this.id}`);
      this.model = res.data;
    },
    // 获取分类
    async fetchCategories() {
      const res = await this.$http.get("rest/categories");
      this.categories = res.data;
      console.log(res)
    },
  },
  created() {
    this.fetchCategories();
    this.id && this.fetch();
  },
};
</script>

